<template>
  <div class="flex items-start justify-center h-screen w-screen overflow-y-auto">
    <Card class="lg:py-8 py-1 lg:px-20 px-1 box-content my-auto text-center shadow-2xl" style="width: 450px" v-if="isSuccess">
      <div class="my-5">
        <img class="mb-16 mx-auto" src="@/assets/icon/mail.svg" alt="Email Sent">
        <h5 class="text-black mb-2">Email has been sent!</h5>
        <p class="text-grey-02 my-8">Please check your inbox. If your email is registered, we have sent a password reset to your email</p>
        <Button color="teal" class="mb-8" :link="true" :to="{ name: 'login' }">Continue</Button>
        <p class="text-center text-grey-02">Don't receive the link? <a href="#" @click.prevent="isSuccess = false">Resend</a></p>
      </div>
    </Card>
    <Card class="lg:py-8 py-1 lg:px-20 px-1 box-content my-auto shadow-2xl" style="width: 450px" v-else>
      <template #default>
        <div class="flex justify-center items-center flex-col mb-12">
          <img src="@/assets/logo.svg" alt="Brand logo" whitelabel="image-replace" data-image-type="logo" width="180">
        </div>
        <p class="text-grey-02 text-center my-8">Enter the email address associated with your account and we'll send you a link to reset your password.</p>
        <div class="mb-6 form">
          <form @submit.prevent="submit">
            <div class="block my-5">
              <Input placeholder="Email Address" v-model="data.email" id="email" :view="validation.email.$invalid && 'danger' || 'default'" block />
              <span class="text-red-base mt-2 block" v-for="error of validation.email.$errors">{{ error.$message }}. </span>
            </div>
            <div class="block my-4 text-center">
              <Button class="mb-10" color="teal" type="submit" block :loading="loading">Continue</Button>
              <router-link :to="{
                name: 'login'
              }">Back to Login</router-link>
            </div>
          </form>
        </div>
<!--        <p class="text-center text-grey-02">Don't have an account? <router-link :to="{ name: 'register' }">Register</router-link></p>-->
      </template>
    </Card>
  </div>
</template>

<script>
import { defineComponent, ref } from 'vue'
import { useForgotPassword } from 'composable/user'
import Card from 'bbUi/components/card/card.vue'
import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'
// import Alert from 'bbUi/components/alert/alert.vue'
import { required, email } from '@/validations/index'
import { useVuelidate } from "@vuelidate/core";

export default defineComponent({
  name: 'ForgotPassword',
  components: {
    Card,
    Input,
    Button
  },
  setup() {
    const { data, loading, action } = useForgotPassword()
    const isSuccess = ref(false)

    const rules = {
      email: { required, email }
    }
    const validation = useVuelidate(rules, data, {
      $lazy: true
    })
    const validate = () => {
      validation.value.$validate()
      return !validation.value.$invalid
    }
    const submit = async () => {
      if(validate()) {
        await action(() => {
          isSuccess.value = true
        })
      }
    }

    return {
      submit,
      isSuccess,
      data,
      loading,
      validation
    }
  }
})
</script>